import axios from 'axios';
import router from '../../router';

let BASE_URL;

if (process.env.NODE_ENV === 'production') {
  BASE_URL = '/';
} else {
  // BASE_URL = 'https://e-voyagayetmaganlar.iiv.uz/'
  BASE_URL = 'https://evoya.technocorp.uz/';
  // BASE_URL = 'http://192.168.1.141:8000/';
  // BASE_URL = 'http://192.168.1.104:8000/';
  // BASE_URL = 'http://192.168.1.115:8001/';
}

const http = axios.create({
  // baseURL: 'http://192.168.1.78:8000/',
  baseURL: BASE_URL,
  // baseURL: 'https://evoya.technocorp.uz/',
  // withCredentials: true,
});

// http.interceptors.response.use(
//   function (response) {
//     return response;
//   },
//   function (error) {
//     console.log(error);
//     const code = error.response.status;
//     if (code === 401) {
//       router.push('/auth');
//     }
//     return Promise.reject(error);
//   }
// );

http.interceptors.request.use(async (config) => {
  let token = localStorage.getItem('access_token'); // => 'value'
  if (token && typeof window !== 'undefined') {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

http.interceptors.response.use(
  (config) => {
    return config;
  },
  async (error) => {
    const originalRequest = error.config;
    if (error?.response?.status === 401 && error.config && !error.config._isRetry) {
      originalRequest._isRetry = true;
      try {
        const refresh = localStorage.getItem('refresh_token');
        const response = await http.get(`api/refresh-token/`, {headers: {Refresh: refresh}});
        localStorage.setItem('access_token', JSON.stringify(response.data.access_token));
        return http.request(originalRequest);
      } catch (error) {
        window.location.href = '/#/auth';
      }
    }

    throw error;
  }
);

export default http;
